"use client"

import hotkeys from "hotkeys-js"
import {
  ArrowLeftIcon,
  ArrowRightIcon,
  FacebookIcon,
  HomeIcon,
  LinkIcon,
  TwitterIcon,
} from "lucide-react"
import { useRouter } from "next/navigation"
import { Fragment, type HTMLAttributes, use, useEffect } from "react"
import { toast } from "sonner"
import { NavItem, type NavItemProps } from "~/components/nav-item"
import { Dock } from "~/components/ui/dock"
import { TooltipProvider } from "~/components/ui/tooltip"

type NavProps = HTMLAttributes<HTMLElement> & {
  promise: Promise<[{ slug: string } | null, { slug: string } | null]>
}

export const Nav = ({ promise, ...props }: NavProps) => {
  // const [isFavorite, setIsFavorite] = useState(false)
  const [previous, next] = use(promise)
  const router = useRouter()

  const actions: (null | NavItemProps)[] = [
    {
      icon: HomeIcon,
      tooltip: "Go Home",
      shortcut: "H",
      onClick: () => router.push("/"),
    },
    // {
    //   icon: EraserIcon,
    //   tooltip: "Request a Change",
    //   shortcut: "R",
    // },
    // {
    //   icon: HeartIcon,
    //   tooltip: "Add to favorites",
    //   shortcut: "L",
    //   isActive: isFavorite,
    //   onClick: () => setIsFavorite(!isFavorite),
    // },
    null,
    {
      icon: ArrowLeftIcon,
      tooltip: "Previous Tool",
      shortcut: "←",
      hotkey: "left",
      isDisabled: !previous,
      onClick: () => router.push(`/${previous?.slug}`),
    },
    {
      icon: ArrowRightIcon,
      tooltip: "Next Tool",
      shortcut: "→",
      hotkey: "right",
      isDisabled: !next,
      onClick: () => router.push(`/${next?.slug}`),
    },
    null,
    {
      icon: LinkIcon,
      tooltip: "Copy Link",
      shortcut: "C",
      onClick: () => {
        navigator.clipboard.writeText(window.location.href)
        toast.success("Link copied to clipboard")
      },
    },
    {
      icon: TwitterIcon,
      tooltip: "Share on Twitter",
    },
    {
      icon: FacebookIcon,
      tooltip: "Share on Facebook",
    },
  ]

  useEffect(() => {
    hotkeys("E", () => {
      router.push("edit")
    })

    return () => hotkeys.unbind("E")
  }, [])

  return (
    <TooltipProvider delayDuration={0} disableHoverableContent>
      <Dock {...props}>
        {actions.map((action, i) => (
          <Fragment key={i}>
            {!action && <Dock.Separator />}
            {action && <NavItem {...action} />}
          </Fragment>
        ))}
      </Dock>
    </TooltipProvider>
  )
}
